<template>
	<div class="correct tb-scroll" :class="{ '_opened': isOpened }">
		<div class="close" @click="toggleVisible">
			<icon icon="times" />
		</div>

		<div class="correct__title color-beige strong small mb-2">
			Исправление ошибок
		</div>

		<div class="correct__info small mb-1 lh-42">
			Выделите кусок текста из любого места и вставьте в поле "Текст с ошибкой". Постарайтесь, чтобы текст содержал минимум 10 символов. Спасибо за вашу помощь!
		</div>

		<div class="correct__form">
			<div class="correct__input">
				<textarea v-model="errorText" placeholder="Текст с ошибкой"></textarea>
			</div>

			<div class="correct__input">
				<textarea v-model="correctText" placeholder="Исправленный текст"></textarea>
			</div>

			<div class="correct__submit mt-2">
				<button class="btn btn-beige" @click="sendCorrect">Отправить</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Correct",

		data() {
			return {
				isOpened: false,
				errorText: '',
				correctText: '',
			}
		},

		methods: {
			toggleVisible() {
				this.isOpened = !this.isOpened;
			},
			sendCorrect() {
				const data = {
					errorText: this.errorText,
					correctText: this.correctText,
					date: new Date(),
					routePath: this.$route.fullPath,
				};
				
				console.log("data", data);
			},
		},

		created() {
			app.$correct = this;
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.correct {
		display: flex;
		flex-direction: column;
		position: absolute;
		right: -330px;
		bottom: 10px;
		z-index: 1;
		height: 400px;
		width: 300px;
		background-color: @color-dark;
		border-radius: 5px;
		transition: right .3s ease-in-out;
		-webkit-box-shadow: inset 0 0 0 1px black;
		-moz-box-shadow: inset 0 -1px 0 1px black;
		box-shadow: inset 0 0 0 1px @color-beige;
		padding: 15px 15px 0;

		&._opened {
			right: 1rem;
		}

		&__input {
			margin-bottom: 0.5rem;
			padding-bottom: 0.5rem;
			border-bottom: dotted 1px @color-beige;
		}

		&__form {
			button {
				width: 100%;
			}
		}

		.close {
			position: absolute;
			top: 10px;
			right: 10px;
			font-size: 14px;
			cursor: pointer;

			&:hover {
				color: @color-pink;
			}
		}
	}
</style>


